<template>
  <div id="playBtn-wrapper" @click="goPlay">
    <div class="iconfont icon-zhuanji"></div>
  </div>
</template>

<script>
export default {
 name:'PlayBtn',
 methods:{
   goPlay() {
     if(this.$store.state.songUrl != '') {
       this.$router.push('/play');
     }else {
       alert('当前未播放音乐！');
     }
   }
 }
}
</script>

<style>
    #playBtn-wrapper {
    display: flex;
    justify-content: center;
    width: 6vh;
    height: 6vh;
    background-color: rgb(200, 0, 0);
    border-radius: 100%;
    box-shadow:1px 1px #a1a1a1;
    line-height: 6vh;
    position: fixed;
    bottom: 5vh;
    right: 5vw;
  }

  .icon-zhuanji {
    color: white;
    font-size: 10vw;
  }
</style>