//当前模块进行api统一管理
import requests from "./request";

export const reqMusic = (keywords) => requests({
  url: `search?keywords=${keywords}`,
  method: 'get'
})

export const reqPicUrl = (id) => requests({
  url: `album?id=${id}`,
  method: 'get'
})

export const reqMusicDetail = (id) => requests({
  url: `song/url?id=${id}`,
  method: 'get'
})