<template>
  <div id="search-wrapper">
    <div id="search">
      <div id="back" @click="backHome" class="iconfont icon-jiantou_xiangzuo">
      </div>
      <div id="inputer">
        <span class="iconfont icon-sousuo"></span>
        <input type="text" @keyup.enter="searchMusic(keywords)" v-model="keywords" placeholder="请输入歌曲、歌手...">
        <span class="iconfont icon-jinrujiantouxiao" @click="searchMusic(keywords)"></span>
      </div>
    </div>
    <div id="search-log" v-show="curShow == 'logList'">
      <ul>
        <li v-for="(log,index) in logList" :key="index" @click="searchMusic(log)">
          <span>{{log}}</span>
          <span class="iconfont icon-cuowu" @click="deleteLog(index)"></span>
        </li>
        <li><span id="clearAll" @click="clearAll">清空所有记录</span></li>
      </ul>
    </div>
    <div id="search-music" v-show="curShow == 'musicList'">
      <ul>
        <li v-for="(m,index) in musicList" :key="index">
          <div id="m-img"><img :src="albumList[index].blurPicUrl || './images/album.png'" alt=""></div>
          <div id="m-data" @click="goPlay(index)">
           <div id="m-name">{{m.name}}</div>
           <div id="m-singer">
             <span v-for="(s,index) in m.artists" :key="index">{{s.name}}</span>
          </div>
          </div>
          <div id="m-download" class="iconfont icon-gengduogongneng">
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name:'Search',
  data() {
    return {
      keywords:'',
      curShow:'logList',
    }
  },
  methods: {
    searchMusic(keywords) {
      if (keywords.trim() != ''){
        this.$store.dispatch('getMusic',keywords);
        this.$store.commit('CLEARALBUM');
        this.curShow = 'musicList';
      }else {
        console.log('输入为空！');
      }
    },
    backHome() {
      this.$router.push('/home');
    },
    deleteLog(index) {
      this.$store.dispatch('deleteLog',index);
    },
    clearAll() {
      this.$store.dispatch('clearAll');
    },
    goPlay(index) {
    this.$router.push({
        name:"play",
        query:{
          id:index
        }
      })
    }
  },
  computed:{
    ...mapState(['logList','musicList','albumList']),
  },
  watch:{
    logList:{
      handler(value){
        localStorage.setItem('logList',JSON.stringify(value))
      }
    }
  }
}
</script>

<style scoped>
   #search-wrapper {
     width: 100%;
     height:100%;
     flex-flow: column;
   }

   #search {
    height: 8vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(200, 0, 0);
    color: white;
   }

   #back {
     width: 20vw;
     height: 100%;
     font-size: 10vw;
   }

   #inputer {
     height: 100%;
     display: flex;
     align-items: center;
   }

   #inputer span {
     height: 100%;
     width: 15vw;
   }

  .icon-sousuo {
    display: flex;
    justify-content: flex-end;
  }

   #inputer input {
     height: 60%;
     background-color: rgb(200, 0, 0);
     font-size: 4vw;
     color: white;
   }

    .iconfont {
    line-height: 8vh;
    font-size: 8vw;
  }

  ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: whitesmoke;
    font-size: 5vw;
  }

  #search-log ul li {
    display: flex;
    justify-content: space-between;
    height: 6vh;
    border-bottom: 1px solid rgb(95, 95, 95);
    line-height: 6vh;
    color: rgb(95, 95, 95);
    font-size: 5vw;
  }

  li span {
    padding: 0 3vw;
  }

  .icon-cuowu {
    display: flex;
    align-items: center;
    font-size: 6vw;
  }

  #clearAll {
    margin: 0 auto;
  }

  #search-music ul li{
    display: flex;
    align-items: center;
    height: 8vh;
  }

  #m-img {
    height: 7vh;
    width: 7vh;
    margin: 0 3vw;
  }

  #m-img img {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    overflow: hidden;
  }

  #m-download {
    height: 100%;
    width:8vw;
  }

  #m-name {
    width: 70vw;
    font-size: 18px;
    font-weight:520;
    overflow: hidden;
  }

  #m-download {
    color: rgb(160, 160, 160);
  }

  #m-singer span {
    padding:0;
    color: rgb(160, 160, 160);
  }

  #m-singer {
    margin-top: .5vh;
  }

</style>