<template>
  <div id="play-wrapper">
    <transition name="play" appear>
      <div>
        <div id="song-header">
        <div id="back" @click="backSearch" class="iconfont icon-jiantou_xiangzuo"></div>
      </div>
      <div id="song-data">
        <span id="song">{{musicList[index].name}}</span>
        <div id="singer">
          <span v-for="(s,index) in musicList[index].artists" :key="index">{{s.name}} </span>
        </div>
      </div>
      <div id="song-img">
        <img :src="albumList[index].blurPicUrl" alt="">
      </div>
      <audio  
        ref="audio"
        type="audio/ogg" 
        :src="'https://music.163.com/song/media/outer/url?id='+musicList[index].id"
        @pause="onPause"
        @play="onPlay"
        @timeupdate="onTimeupdate" 
        @loadedmetadata="onLoadedmetadata"
        >
      </audio>
      <div id="progress-bar">
        <div id="bar">
          <el-slider 
          v-model="sliderTime" 
          :format-tooltip="formatProcessToolTip" 
          @change="changeCurrentTime" 
          class="slider"
          ></el-slider>
        </div>
        <div id="time">
          <span>{{ audio.currentTime | formatSecond}}</span>
          <span>{{ audio.maxTime | formatSecond}}</span>
        </div>
      </div>
      <div class="btn">
        <span class="play-btn iconfont icon-shangyishou"></span>
        <span class="play-btn iconfont" @click="playOrPause" :class="audio.playing ? 'icon-zanting':'icon-24gf-play'"></span>
        <span class="play-btn iconfont icon-xiayishou"></span>
      </div>
      <div class="btn1">
        <span class="play-btn1 iconfont icon-liebiao"></span>
        
          <a 
          href="#" 
          class="download play-btn1 iconfont icon-xiazai1" 
          >
          </a>
        
        <span class="play-btn1 iconfont icon-loop"></span>
      </div>
      </div>
    </transition>
  </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
  name:'Play',
  data() {
    return {
      audio:{
        playing:false,
        currentTime:0,
        maxTime:0,
      },
      sliderTime:0
    }
  },
  computed:{
    index() {
      return this.$route.query.id || this.$store.state.curMusicID;
    },
    ...mapState(['albumList','musicList','songUrl']),

  },
  mounted() {
    this.$store.dispatch('getMusicDetail',this.musicList[this.index].id);
    if(this.$route.query.id) {
      this.$store.commit('CURMUSIC',this.$route.query.id);
    }
  },
  methods:{
    backSearch() {
      this.$router.push('/search');
    },
    //音乐播放控件
    playOrPause() {
      return this.audio.playing ? this.pause() : this.play();
    },
    play() {
      this.$refs.audio.play();
    },
    pause() {
      this.$refs.audio.pause();
    },
    onPlay() {
      this.audio.playing = true;
    },
    onPause() {
      this.audio.playing = false;
    },
    //时间控制
    onTimeupdate(res) {
      this.audio.currentTime = res.target.currentTime;
    },
    onLoadedmetadata(res) {
      this.audio.maxTime = parseInt(res.target.duration);
    },
    // 拖动进度条，改变当前时间，index是进度条改变时的回调函数的参数0-100之间，需要换算成实际时间
    changeCurrentTime(index) {
      this.$refs.audio.currentTime = parseInt(index / 100 * this.audio.maxTime)
    },
    // 当音频当前时间改变后，进度条也要改变
    onTimeupdate(res) {
      //console.log('timeupdate')
      //console.log(res)
      this.audio.currentTime = res.target.currentTime
      this.sliderTime = parseInt(this.audio.currentTime / this.audio.maxTime * 100)
    },

    // 进度条格式化toolTip
    formatProcessToolTip(index = 0) {
      index = parseInt(this.audio.maxTime / 100 * index)
      return '进度条: ' + realFormatSecond(index)
    },
  },
  filters: {
    // 将整数转化成时分秒
    formatSecond(second) {
      return realFormatSecond(second);
    }
  }
}

function realFormatSecond(second) {
  var secondType = typeof second

  if (secondType === 'number' || secondType === 'string') {
    second = parseInt(second)

    var hours = Math.floor(second / 3600)
    second = second - hours * 3600
    var mimute = Math.floor(second / 60)
    second = second - mimute * 60

    return hours + ':' + ('0' + mimute).slice(-2) + ':' + ('0' + second).slice(-2)
  } else {
    return '0:00:00'
  }
}
</script>

<style scoped>
  #play-wrapper {
    width: 100%;
    height: 100%;
    background-color: whitesmoke;
  }

  #song-header {
    height: 5vh;
    background-color: rgb(200, 0, 0);

  }

  #back {
     width: 20vw;
     line-height: 5vh;
     font-size: 10vw;
     color: whitesmoke;
     padding-left: 3vw;
   }

  #song-data {
    height: 10vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  #song {
    font-size: 24px;
  }

  #singer {
    font-size: 16px;
  }

  #song-img {
    height: 80vw;
    width: 80vw;
    margin:0 auto;
  }

  #song-img img {
    width: 100%;
    height: 100%;
  }

  #progress-bar {
    height: 14vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding: 3vh 3vw;
  }

  #bar {
    width: 100%;
  }

  audio {
    height: 100%;
    width: 100%;
    background-color: whitesmoke;
    border-radius: 0;
  }

  .btn,.btn1 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 15vh;
  }

  .btn1 {
    margin-top: 3vh;
    height: 10vh;
  }

  .play-btn {
    height: 10vw;
    font-size:10vw;
  }

  .play-btn1 {
    height: 8vw;
    font-size: 8vw;
  }

  #time {
    display: flex;
    justify-content: space-between;
  }

  .slider {
    height: 5vh;
  }

  .download {
    color: #333;
  }

  #bar /deep/ .el-slider__bar {
    background-color: rgb(200, 0, 0);
  }

  #bar /deep/ .el-slider__button {
    border: 2px solid rgb(200, 0, 0);
    width: 14px;
    height: 14px;
    background-color: rgb(200, 0, 0);
  }

  .play-enter,.play-leave-to {
    transform: translateY(-100%);
  }

  .play-enter-active,.play-leave-active {
    transition: 0.5s linear;
  }

  .play-leave,.play-enter-to {
    transform: translateY(0);
  }
</style>