//该文件用于创建Vuex中最核心的store
//引入vuex
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

//引入封装好的axios
import { reqMusic, reqPicUrl, reqMusicDetail } from '@/api';
//准备actions——用于响应组件中的动作
const actions = {
  //获取音乐概览列表
  async getMusic(context, value) {
    //记录关键词
    context.commit('LOGLIST', value);
    //发送请求
    let result = await reqMusic(value);
    //console.log(result);
    if (result.code == 200) {
      let p = new Promise(async function(resolve, reject) {
        let songs = result.result.songs;
        for (let i = 0; i < songs.length; i++) {
          let result1 = await reqPicUrl(songs[i].album.id);
          //console.log(result1);
          if (result1.code == 200) {
            context.commit('ALBUMLIST', result1.album);
          }
        }
        resolve();
      });
      //先将图片全部请求到，再渲染dom
      p.then(function(data) {
        context.commit('GETMUSIC', result.result.songs);
      })
    }
  },
  initLogList(context) {
    if (localStorage.getItem('logList')) {
      context.commit('INITLOGLIST', JSON.parse(localStorage.getItem('logList')));
    }
  },
  deleteLog(context, index) {
    context.commit('DELETELOG', index);
  },
  clearAll(context) {
    context.commit('CLEARALL');
  },
  //获取音乐详细列表
  async getMusicDetail(context, id) {
    let result = await reqMusicDetail(id);
    //console.log(result);
    if (result.code == -462) {
      context.commit('GETMUSICDETAIL', result.data);
    }
  }
}

//准备mutations——用于响应组件中的动作
const mutations = {
  GETMUSIC(state, value) {
    state.musicList = value;
  },
  LOGLIST(state, value) {
    if (state.logList.indexOf(value) == -1) {
      state.logList.push(value);
    }
  },
  INITLOGLIST(state, value) {
    state.logList = value;
  },
  DELETELOG(state, index) {
    state.logList.splice(index, 1);
  },
  CLEARALL(state) {
    state.logList = [];
  },
  ALBUMLIST(state, value) {
    state.albumList.push(value);
  },
  CLEARALBUM(state) {
    state.musicList = [];
    state.albumList = [];
  },
  GETMUSICDETAIL(state, value) {
    state.songUrl = value.url;
  },
  CURMUSIC(state, value) {
    state.curMusicID = value;
  }
}

//准备state——用于响应组件中的动作
const state = {
  musicList: [],
  logList: JSON.parse(localStorage.getItem('logList')) || [],
  albumList: [],
  songUrl: '',
  curMusicID: NaN
}

//准备getters——用于将state中的数据进行加工
const getters = {}

//创建并暴露store
export default new Vuex.Store({
  actions: actions,
  mutations: mutations,
  state: state,
  getters: getters,
})